t:500;font-size:1.2rem;color:#ff5757}section.template h2{padding-top:0.25rem;font-weight:700;font-size:2rem;color:#242A55}section.Nav{background-color:#f7f8fa;top:0;position:fixed;left:0;right:0}section.Nav .wrapper{height:5rem;position:relative;width:100%}section.Nav .outer{height:3.4rem;height:100%}section.Nav .outer .inner{padding:0 1.25rem}section.Nav .outer .inner .flex .left{width:20%}section.Nav .outer .inner .flex .right{width:80%}section.Nav .outer .inner .flex .left{color:#3f4358;font-weight:700;font-size:1.5rem}section.Nav .outer .inner .flex .right .inner{padding:0 !important}section.Nav .outer .inner .flex .right .inner .most{align-items:baseline;flex-wrap:nowrap}section.Nav .outer .inner .flex .right .inner .most .list{justify-content:flex-end;width:100%;align-items:center}section.Nav .outer .inner .flex .right .inner .most .list .list-item{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:0 1.25rem 0 0.75rem;font-size:1rem;color:#6f759c;font-weight:600}section.Nav .outer .inner .flex .right .inner .most .list .user{padding:0 1rem}section.Nav .outer .inner .flex .right .inner .most .list .user .button{margin:0 0.75rem 0 0.25rem}section.Nav .outer .inner .flex .right .inner .most .list .ham{height:1.5rem;width:1.5rem;background-color:#3f4358}.SideBar{position:absolute;margin-top:5rem;top:0;z-index:2;overflow:none;width:100vw;height:calc(100vh - 5rem);background-color:#f6f6f6;transition:visibility,opacity;transition-delay:0.2s;transition-duration:0.5s;transition-timing-function:ease-in-out}.SideBar .inner .item{font-size:1rem;color:#6f759c;font-weight:600;text-align:center;font-size:1.3rem;margin:10px 20px;padding:8px}.SideBar .inner .button{text-align:center;margin:20px 20px}body{margin:0;font-family:'Open Sans';-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}#root{width:100%;overflow-x:none}

